@import '../../styles/global.scss';



/* --- PAGE UP BUTTON --- */

.pageup-button {
    // display: none;
    position: fixed;
    right: -100px;
    bottom: 10vh;
    z-index: 9999;

    padding: 0.5rem;
    border-radius: 1rem 0 0 1rem;
    border: none;
    background-color: var(--white);
    box-shadow: 10px 0px 50px 0px var(--gray);

    transition: all 0.2s ease-in-out;
}

.pageup-button img {
    display: block;
    width: 3rem;
    height: 3rem;
}

.pageup-button.visible {
    @include for-size(small) {
        // display: block;
        right: 0;
    }
}



/* --- SUB NUVIGATION --- */

.filter-tags {

}

.filter-tags ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0.5rem 2rem;
}

.filter-tags li {
    list-style-type: none;
    margin: 0 0.5rem;
}

.filter-tags li a {
    font-size: larger;
    font-style: italic;
    font-weight: bold;
    text-decoration: underline;
    color: var(--blue);
}

.filter-tags li a:hover {
    text-decoration: none;
    color: var(--red);
}



/* --- PROJECTS GRID ---  */

.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
    gap: 2rem 1rem;

    margin: 1rem;
}

.projects-card img {
    display: block;
    width: 100%;
    border-radius: 10px;
}

.projects-card h3 {
    margin-top: 0.5rem;
    margin-bottom: 0;
}

.projects-card p {
    display: inline;
    margin: 0.1rem 0;
    font-style: italic;
    color: var(--gray);
}