/* --- BODY --- */

body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: hsl(0, 0%, 100%);
}

.showreel-container {
  display: flex;
  flex-direction: column;
}

.showreel-item {
  margin: 0;
  padding-top: 20vw;
  padding-bottom: 20vw;
}

.showreel-item h1 {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-style: italic;
  text-align: center;
  text-transform: capitalize;
  color: white;
}

.featured {
  background-color: rgb(234, 51, 36);
}

.vk-masks {
  background-color: rgb(96, 207, 250);
}

.ar-effects {
  background-color: rgb(181, 37, 246);
}

.game-dev {
  background-color: rgb(244, 183, 63);
}

