@import '../../styles/global.scss';



/* --- FOOTER --- */

footer {
    margin: 0;
    padding: 2rem 0;
    text-align: center;
    color: var(--gray);
    background-color: var(--black);
  }



/* --- NAVIGATION MENU --- */

footer .navigation-menu ul {
    display: flex;
    padding: 0;
    margin: 0;
    justify-content: center;

    @include for-size(phone) {
        flex-direction: column;
        margin: 0 10%;
    }
}

footer .navigation-menu li {
    list-style-type: none;
    margin: 0 0.5rem;
    @include for-size(phone) {
        padding: 0.5rem 0.1rem;
        border-bottom: 0.01rem solid var(--gray);
    }
}

footer .navigation-menu li a {
    display: block;
    font-size: 1rem;
    font-weight: bold;
    text-decoration: none;
    color: var(--grey);
    transition: color 0.1s linear;
    @include for-size(phone) {
        text-align: left;
    }
}

footer .navigation-menu li a:hover,
footer .navigation-menu.active {
    color: var(--white);
}



/* --- SOCIAL MEDIA ICONS --- */

footer .social-media-links ul {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 0;
    margin: 0;
}

footer .social-media-links li {
    list-style-type: none;
    margin: 0 0.5rem 0 0;
}

footer .social-media-links img {
    display: block;
    height: 1.5rem;
    filter: invert(100%) opacity(80%);
    transition: filter 0.1s linear;
}

footer .social-media-links img:hover {
    filter: invert(100%)  opacity(100%);
}



/* --- LAYOUT --- */

footer p {
    margin: 0;
    padding: 0;
}

footer .container {
    display: grid;
    grid-template-columns: 1fr 2fr;

    margin: 5% 10%;
}

footer .item {
    text-align: left;
    vertical-align: top;
    padding-left: 10px;
}

footer .item-logo {
    text-align: right;
    grid-row: span 4;
}



/* --- TEXT --- */

.factotum-red {
    color: rgb(193, 72, 67);
    font-weight: bold;
}

.text {
    color: inherit;
    font-size: medium;

    @include for-size(small) {
        padding-right: 2rem;
        font-size: smaller;
    }
}

.year {
    font-size: medium;

    @include for-size(small) {
        font-size: small;
    }
}