:root {
    --white: hsl(0, 0%, 100%);
    --gray: hsl(0, 0%, 50%);
    --black: hsl(0, 0%, 0%);
    --red: hsl(0, 95%, 44%);
    --blue: hsl(208, 100%, 50%);
}

@mixin for-size($size) {
    @if $size == phone {
        @media screen and (max-width: 599px) {
            @content
        }
    }
    @else if $size == small {
        @media screen and (max-width: 730px) {
            @content;
        }
    }
}