@import '../../styles/global.scss';



/* --- HEADER --- */

header {
    --logo-image-height: calc(10vh + 8vw);
    --navbar-height: 2.5rem;

    min-height: calc(var(--logo-image-height) + var(--navbar-height));
    background-color: var(--black);
}



/* --- LOGO IMAGE --- */

header .logo-image {
    display: flex;
    justify-content: center;

    height: var(--logo-image-height);
}

header .logo-image img {
    height: 100%;
}



/* --- NAVIGATION BAR --- */

header .navigation-bar {
    height: var(--navbar-height);
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: var(--red);

    @include for-size(small) {
        justify-content: start;
        overflow: scroll;
        white-space: nowrap;
    }
}

header .sticky {
    position: fixed;
    top: 0;
}

header .navigation-bar::-webkit-scrollbar {
    @include for-size(small) {
        display: none;
    }
}




/* --- NAVIGATION MENU --- */

header .navigation-menu ul {
    display: flex;
    padding: 0;
    margin: 0;

    @include for-size(small) {
        margin-left: 0.5rem;
    }
}

header .navigation-menu li {
    list-style-type: none;
    margin: 0 0.5rem;
}

header .navigation-menu li a {
    display: block;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    color: var(--black);

    transition: color 0.1s linear;
}

header .navigation-menu li a:hover,
header .navigation-menu.active {
    color: var(--white);
}



/* --- SOCIAL MEDIA ICONS --- */

header .social-media-links ul {
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0 0.25rem;

    @include for-size(small) {
        margin-right: 0.5rem;
    }
}

header .social-media-links li {
    list-style-type: none;
    margin: 0 0.25rem;
}

header .social-media-links img {
    display: block;
    height: 1.25rem;
    margin: auto;
    filter: invert(0) opacity(75%);
    transition: filter 0.1s linear;
}

header .social-media-links img:hover {
    filter: invert(1);
}